也沒有那個為了生態可以一起討論的氣氛。

「抗爭未完！」雖以非在第一線衝刺的戰將，但林
聖崇提醒，公民的壯大將是台灣延續的重要基礎，
每一個世代從上一代接手，都有各自要做的使命。
「最大的影響一定要來自民間」在積極參與環運、
社運的三四十年生涯、近年宜居加拿大溫哥華後
仍持續參與公共事務，這是林聖崇最大的體會。
林聖崇以加拿大語言複雜的狀況為例，政府裡的
菁英如何讓所有語言的民眾都知道政策，又能如
